@import 'variables';
@import 'mixins';

.line{
    margin: 8% auto;
    width: 94%;
    border-top: 4px solid black;
    border-top-style: dashed;

    @media (min-width: 581px){
        margin: 5% auto;
    }

    @media (min-width: 1024px){
        margin: 3% auto;
    }
}

.tasklist__title{
  @include title
}

.tasklist__container{
    margin-left: 3%;
    margin-right: 3%;
    font-size: 1.2rem;
    margin-bottom: 3%;

    @media (min-width: 781px){
        font-size: 1.4rem;
    }

    @media (min-width: 1024px){
        font-size: 1.6rem;
    }

    .tasklist__circle{
       font-size: .8rem;
       vertical-align: middle;
    }

    .tasklist__task{
        padding: 10px 0 2px;
        border-bottom: 1px solid gray;
    }

    .tasklist__icons{
        width: 100%;
        text-align: center;
        font-size: 1.5rem;
    }

    .tasklist__complete, .tasklist__edit, .tasklist__delete{
        cursor: pointer;
        margin: 0 5px;
    }
}